.header{
    background-color: #222;
    width: 100vw;
    height: 150px;
    margin: auto;
    position: fixed;
    display: block;
    right: 0;
    left: 0;
    top: 0;
}
.header-image{
    width: 100px;
    height: 100px;
    position: absolute;
    margin: auto;
    top: 0;
    bottom: 0;
    right: 0;
    left: 0;
}
.header-title{
    color: white;
    position: absolute;
    text-align: center;
    margin: auto;
    top: 100;
    bottom: 0;
    right: 0;
    left: 0;
}
.menu{
    //background-color: #224;
    position: fixed;
    display: block;
    margin: auto;
    padding: auto;
    top: 150px;
    left: 0;
    right: 0;
}
.buttons{   
    width: 25%;
    height: 50px;
}
.article{
    background-color: #321;
    position: fixed;
    display: block;
    width: 100vw;
    height: 100vh-200;
    margin: auto;
    top: 200px;
    right: 0;
    left: 0;
    bottom: 0;
}
.gallery {
    margin: 5px;
    border: 1px solid #ccc;
    float: left;
    width: 100%;
    height: 100%;
    //position: fixed;
    margin: auto;
    padding: auto;
    //padding-top: 5px;
    left: 0;
    right: 0;
}
.images{
    width: 32.3%;
    height: 25%;
    padding: 1px;
    //position: absolute;

}



